import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import BattleCard from './BattleCard';
import { Link } from "react-router-dom";
import urls from '../../../../utils/endPoints';

import './Battles.css'

export default function Battles(props) {

  let [battles, setBattles] = useState([]);
  useEffect(() => {
    const data = {
      application_id: props.match.params.id,
      user_id: "61c5cbfa3c07accb12c8eaf3"
    };
    axios.post(`${urls.base_url}/application/battles-view`, data, {
      headers: {
      },
    }).then(res => {
      setBattles(res.data && res.data.battles && res.data.battles.length > 0 ? res.data.battles : []);
    });
    // return applications.data
  }, []);

  return (
  <div>
    <div className='row justify-content-end'>
      <div className="col-3 col-sm-9">
          <h3 className='heading'>Games</h3>
      </div>
      <div className="col-1 col-sm-3 m-auto">
          <Link to={`/home/battle-form/${props.match.params.id}`} className="btn btn-primary event-btn">+ Create Event</Link>
      </div>
    </div>
    <div className="row mr-2">
      {battles && battles.battles && battles.battles.length === 0 ? <h1>No Applicaitons found</h1> :
        battles.map((data,index) => (
          <div key={data.id} className="col-sm-12 col-md-6 col-lg-6">
            <BattleCard
              index={index}
              key={data.id}
              battle={data}
            />
          </div>
        ))
      }
    </div>
  </div>
)}
