import React from 'react';
import Sidebar from '../../sidebar/Sidebar';
import { useHistory,Link,Switch,Route,BrowserRouter } from "react-router-dom";
import Battles from '../games/battles/Battles';
import { GameEventForm } from '../games/GameBattleForm';
import { GameForm } from '../games/GameForm';
import { RoomsTable } from '../games/Rooms/RoomsTable';
import RoomDetails from '../games/Rooms/RoomDetails/RoomDetails';
import Navbar from '../../navbar/Navbar';
import Main from '../Main';
import './Home.css'

const Home = () => {
    const [sidebarOpen, setsidebarOpen] = React.useState(false);
    const openSidebar = () => {
      setsidebarOpen(true);
    };
    const closeSidebar = () => {
      setsidebarOpen(false);
    };
  return (
  <div className='flex' >
      <Sidebar sidebarOpen={sidebarOpen} closeSidebar={closeSidebar}/>
      <div className='content'>
        <Navbar />  
        <Switch>
          <Route path="/home/games" component={Main} exact/>
          <Route path="/home/game-details/:id" component={Battles} exact/>
          <Route path="/home/battle-form/:id" component={GameEventForm} exact/>
          <Route path="/home/game-form" component={GameForm} exact/>
          <Route path="/home/rooms/:id" component={RoomsTable} exact/>
          <Route path="/home/room/details/:id" component={RoomDetails} exact/>
        </Switch>
      </div>
  </div>
)};

export default Home;
