import React, { useState, useEffect } from 'react'
import axios from 'axios';
import GameCard from './GameCard';
import { Link } from "react-router-dom";
import urls from '../../../utils/endPoints'
import { GameEventForm } from './GameBattleForm';

import './Game.css'

export default function Game() {

    let [applications, setApplications] = useState([]);
    useEffect(() => {
        axios.post(`${urls.base_url}/application-view`).then(res => {
            setApplications(res.data && res.data.apps && res.data.apps.length > 0 ? res.data.apps : []);
        });
        // return applications.data
    }, []);

    return (
    <div>
        <div className="row">
            <div className="col-sm-9  col-lg-9">
                <h4>Games</h4>
            </div>
            <div className="col-sm-3 col-lg-3">
                <Link to='/home/game-form' className="btn btn-primary event-btn">+ Create Game</Link>
            </div>
            <div className="col-11 pl-0">
                {applications && applications.apps && applications.apps.length === 0 ? 
                <h1>No Applicaitons found</h1> :
                    applications.map((data,index) => (
                        <div key={index} className="col-sm-12 col-md-6 col-lg-4 pl-0">
                            <GameCard
                                key={data.id}
                                application={data}
                            />
                        </div>
                    ))
                }
            </div>
        </div>
    </div>
)}
