import React from 'react';
import PositionCard from './PositionCard';

const Positions = (props) => {
  const {battlers} = props
  return (
  <div>
      {
        battlers.length > 0 ?
        battlers.slice(0,2).map((battler,index)=>{
          return(
            <PositionCard key={index} pos={index} battler={battler}/>
          )
        }) :
        <p className='text-white' >NO DATA</p>
      }
  </div>
)};

export default Positions;
