import React from 'react';
import { useHistory , Link} from 'react-router-dom';
import urls from '../../../utils/endPoints'
import './GameCard.css'
export default function GameCard(props) {
    let history = useHistory()
    return (
    <div className='game-card row'>
        <div className="col-4 pt-10 pr-10 pb-10">
            <div className="card">
                <img src={`${urls.base_url_img}/${props.application.app_icon}`} alt="Avatar" />
            </div>
        </div>
        <div className="col-8 pl-10 pt-10 pr-10 pb-10">
            <p>{props.application.app_name}</p>
            <Link className='btn game-card-link' to={`/home/game-details/${props.application._id}`}>
                <i className='fa fa-eye mr-2' ></i>
                <span>View Details</span>
            </Link>
        </div>
    </div>
    )
    
}
