import React from 'react';
import dp from '../../../../../../assets/images/img.png'
import { ReactComponent as CrownIcon} from '../../../../../../assets/crown.svg'
import { ReactComponent as First} from '../../../../../../assets/first.svg'
import { ReactComponent as Second} from '../../../../../../assets/second.svg'
import { ReactComponent as Third} from '../../../../../../assets/third.svg'
import './PositionCard.css'

const PositionCard = (props) => {
    const {pos,battler} = props

    const setClass = () => {
        if(pos === 0 ){
            return `pos-card-img img-border-1`
        }
        else if(pos === 1 ){
            return `pos-card-img img-border-2 mt-5`
        }
        else {
            return  `pos-card-img img-border-3 mt-5`
        }
    }

    const setIcon = () => {
        if(pos === 0 ){
            return <First className='pos-icon'/>
        }
        else if(pos === 1 ){
            return <Second className='pos-icon'/>
        }
        else {
            return  <Third className='pos-icon'/>
        }
    }
    return (
        <div>
            <div className="card mb-3 pos-card d-flex justify-content-center align-items-center">
                <div className="row no-gutters ">
                    <div className="col-md-4 img-sec">
                        {
                            pos === 0 ?
                            <CrownIcon className='crown'/> : <></>
                        }
                        <img src={dp} className={setClass()} alt="..."/>
                        {setIcon()}
                    </div>
                    <div className="col-md-8 d-flex flex-column justify-content-center align-items-center ">
                        <div className="card-body d-flex flex-column justify-content-center align-items-center ">
                            <h5 className="card-title">Natasha Foster</h5>
                            <p className="card-title"><span className='points' >Points: </span>500</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
)};

export default PositionCard;
