import React from 'react';
import { ReactComponent as DiamondIcon } from '../../../../assets/diamond-logo.svg'
import { ReactComponent as CoinsIcon } from '../../../../assets/coins-icon.svg'
import { ReactComponent as CircleIcon } from '../../../../assets/circle-icon.svg'
import { ReactComponent as EyeIcon } from '../../../../assets/eye-icon.svg'

import { Link } from 'react-router-dom';

import './BattleCard.css'

export default function BattleCard(props) {
    const {battle} = props
    return (
    <Link to={`/home/rooms/${ battle._id}`} >
        <div className="card battle-card mb-3 pb-3">
            <div className="row no-gutters">
                <div className="col-md-3 circle-sec ">
                    <span className="dot d-flex flex-column justify-content-center align-items-center">
                        <p>{ battle.battle_name}</p>
                    </span>
                    <h5 className="available text-center ml-4 mt-4"><CircleIcon className='mr-2'/> Available</h5>
                    {/* <h5 className="details text-center ml-4"><EyeIcon style={{'width':10}} className='mr-2'/>View Details</h5> */}
                </div>
                <div className="col-md-9 body-sec">
                    <div className="card-body">
                        <div className='row' >
                            <div className='col' >
                                <h5 className="card-title labels">Start Time : <span className='values'>NA</span> </h5>
                                <h5 className="card-title labels">End Time : <span className='values'>NA</span></h5>
                            </div>
                            <div className='col' >
                                <h5 className="card-title labels">Required Gems : 
                                    <span className='values'>{ battle.required_gems || "NA"} <DiamondIcon className='battle-diamond-icon ml-2'/></span>
                                </h5>
                                <h5 className="card-title labels">Required Token : 
                                    <span className='values'>{ battle.required_tokens || "NA"} <CoinsIcon style={{'width':10}} className='battle-coins-icon ml-2' /> </span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div className="card-body">
                        <div className='row' >
                            <div className='col' >
                                <h5 className="card-title labels">Min Time : <span className='values'>NA</span></h5>
                                <h5 className="card-title labels">Max Time : <span className='values'>NA</span></h5>
                            </div>
                            <div className='col' >
                                <h5 className="card-title labels">Min Users : <span className='values'>{ battle.min_players || "NA"}</span> </h5>
                                <h5 className="card-title labels">Max Users : <span className='values'>{ battle.max_players || "NA"}</span> </h5>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </Link>
    )
}
