import React from 'react';
import { useHistory,Link } from "react-router-dom";
import { ReactComponent as DiamondIcon } from '../../../assets/diamond-logo.svg'
import { ReactComponent as CoinsIcon } from '../../../assets/coins-icon.svg'
import axios from 'axios';
import urls from '../../../utils/endPoints'
import './GameBattleForm.css'

export const GameEventForm = (props) => {

  let history = useHistory()
  const [battleName,setBattleName] = React.useState('')
  const [battleDuration,setBattleDuration] = React.useState('')
  const [minimumPlayers,setMinimumPlayers] = React.useState('')
  const [maximumPlayers,setMaximumPlayers] = React.useState('')
  const [minimumWaitingTime,setMinimumWaitingTime] = React.useState('')
  const [maximumWaitingTime,setMaximumWaitingTime] = React.useState('')
  const [requiredGems,setRequiredGems] = React.useState('')
  const [requiredToken,setRequiredTokens] = React.useState('')
  const [minThresholdTime,setMinThresholdTime] = React.useState('')
  const [gameMoves,setGameMoves] = React.useState('')

  const submitForm = (e) =>{
    e.preventDefault()
    const data = {
      application_id:props.match.params.id,
      battle_name:battleName,
      min_players:minimumPlayers,
      max_players:maximumPlayers,
      required_gems:requiredGems,
      required_tokens:requiredToken,
      min_threshold_time:minThresholdTime,
      battle_duration:battleDuration,
      game_moves:gameMoves
    }
    axios.post(`${urls.base_url}/application/battles-create`,data)
    .then(res=>{
      console.log(res.data)
      window.alert('success')
      history.push(`/home/game-details/${props.match.params.id}`)
    })
    .catch(err=>{
      console.log(err)
      window.alert('error')
    })
  }

  return (
  <form onSubmit={submitForm}>
    <h6 className='main-heading'>Create Event</h6>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Battle Name</label>
        <input type="text" onChange={e=>{
          setBattleName(e.target.value)
        }} className="form-control form-input" />
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings'>Required Gems</label>
        <div className="input-icons d-flex bet">
            <DiamondIcon className='mt-3 ml-4 diamond-icon'/>
            <input onChange={e=>{
              setRequiredGems(e.target.value)
            }} className="form-input form-control"  min='0' type="number"/>
        </div>
      </div>
    </div>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Required Tokens</label>
        <div className="input-icons d-flex bet">
            <CoinsIcon style={{'width':15}} className='mt-2 ml-4 coins-icon'/>
            <input onChange={e=>{
              setRequiredTokens(e.target.value)
            }} className="form-input form-control" min='0' type="number"/>
        </div>
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings' >Battle Duration</label>
        <input onChange={e=>{
            setBattleDuration(e.target.value)
          }} type="number" min='0' className="form-control form-input"/>
      </div>
    </div>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Minimum Players</label>
        <input onChange={e=>{
              setMinimumPlayers(e.target.value)
            }} type="number" min='0' className="form-control form-input" />
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings' >Maximum Players</label>
        <input onChange={e=>{
              setMaximumPlayers(e.target.value)
            }} type="number" min='0' className="form-control form-input"/>
      </div>
    </div>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Minimum Waiting Time (Minutes)</label>
        <input onChange={e=>{
              setMinimumWaitingTime(e.target.value)
            }} type="number" min='0' className="form-control form-input" />
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings' >Maximum Waiting Time (Minutes)</label>
        <input
         onChange={e=>{
          setMaximumWaitingTime(e.target.value)
        }}  type="number" className="form-control form-input" />
      </div>
    </div>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Minimum Threshold Time</label>
        <input onChange={e=>{
            setMinThresholdTime(e.target.value)
          }} type="number" min='0' className="form-control form-input" />
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings'>Game Moves</label>
        <input onChange={e=>{
            setGameMoves(e.target.value)
          }} type="number" className="form-control form-input" />
      </div>
    </div>
    <div className="form-group btn-group col-md-2 float-right mt-4">
        <button className="btn form-btn">Cancel</button>
        <button type="submit" className="btn form-btn-create ml-2">Create</button>
    </div>
  </form>
  )
};
