import React from 'react';
import dp from '../../../../../assets/images/im.png'
import './RoomDetailsTable.css'

export const RoomDetailsTable = (props) => {
  const {battlers} = props
  return (
    <div>
      <div className='table-wrapper' >
        <h6 className='table-main-heading mb-4'>Leaderboard</h6>
        <table className="table table-borderless">
          <thead>
            <tr className='detail-tr'>
              <th className='text-center' scope="col">Rank</th>
              <th className='text-center' scope="col">Player Name</th>
              <th className='text-center' scope="col">Total Gems Waged</th>
              <th className='text-center' scope="col">Points</th>
            </tr>
          </thead>
          <tbody>
            {
              battlers.length > 0 ?
              battlers.map((battler,index)=>(
              <tr key={index} className='detail-tr'>
                <td className='text-center' scope="row">{index+1}</td>
                <td className='text-center'><img src={dp} alt="..."/>{battler.user_name}</td>
                <td className='text-center'>{battler.battle_gems}</td> 
                <td className='text-center '><span className='points' >500</span>pts</td>
              </tr>
              )) :
              <></>
            }
          </tbody>
        </table>
      </div>
    </div>
  )
};
