import React from 'react';
import axios from 'axios';
import urls from '../../../../utils/endPoints';
import {useHistory,Link} from 'react-router-dom';

import './RoomTable.css'

export const RoomsTable = (props) => {

  const [rooms,setRooms] = React.useState([])

  let history = useHistory()

  React.useEffect(()=>{
    axios.post(`${urls.base_url}/admin/battle-details`,{
      battle_id:props.match.params.id
    })
    .then(res=>{
      // console.log(res.data.room)
      setRooms(res.data.rooms)
    })
    .catch(err=>{
      console.log(err)
    })
  },[])


  return (
    <div className='table-wrapper' >
      <h6 className='main-heading mb-4'>Rooms</h6>
      <table className="table table-borderless">
        <thead>
          <tr className='table-tr'>
            <th className='text-center' scope="col">#</th>
            <th className='text-center' scope="col">Status</th>
            <th className='text-center' scope="col">Levels</th>
            <th className='text-center' scope="col">Players Required</th>
            <th className='text-center' scope="col">Players Joined</th>
            <th className='text-center' scope="col">Winner</th>
          </tr>
        </thead>
        <tbody>
         {
           rooms.length > 0 ?
           rooms.map((room,index)=>{
             return (
              <tr className='table-tr' key={index} onClick={e=>{
                history.push(`/home/room/details/${room._id}`)
              }} >
                <td className='text-center' scope="row">{index+1}</td>
                <td className='text-center'>{room.room_status==2 && 'Complete'}</td>
                <td className='text-center'>{room.room_level}</td>
                <td className='text-center'>{room.min_players}</td>
                <td className='text-center'>{`${room.players_joined}/${room.max_players}`}</td> 
                <td className='text-center'>{room.winner_name}</td>
              </tr>
             )
           }) :
            <tr>
              <td>Not Available</td>
            </tr>
         }
          
        </tbody>
      </table>
    </div>
  )
};
