import React from 'react'
import LoginForm from './LoginForm'
import LogoArea from './LogoArea'

export default function Login() {
    return (
        <div>
            <div className="row main-background">
                <div className="d-none d-md-block d-lg-block col-sm-0 col-md-4 col-lg-4 m-0 p-0">
                    <LogoArea />
                </div>
                <div className="row col-sm-12 col-md-8 col-lg-8 login-form-container p-0 m-0">
                    <LoginForm/>
                </div>
            </div>
        </div>
    )
}
