import React from 'react';
import axios from 'axios';
import urls from '../../../utils/endPoints'

import './GameBattleForm.css'

export const GameForm = () => {

  const [appName,setAppName] = React.useState('')
  const [file, setFile] = React.useState('')

  const submitForm = (e) => {
    e.preventDefault()
    let formData = new FormData();
    formData.append('app_name',appName)
    formData.append('icon',file)
    formData.append('user_id','61c5cbfa3c07accb12c8eaf3')
    if(appName.length === 0  || file === (undefined || null )){
      window.alert('form incomplete')
    }
    else {
      axios.post(`${urls.base_url}/application`,formData)
      .then(res=>{
        console.log(res.data)
        window.alert('successful')
      })
      .catch(err=>{
        console.log(err)
        window.alert('error')
      })
    }
  }
  return (
  <form onSubmit={submitForm}>
    <h6 className='main-heading'>Create Game</h6>
    <div className="form-row py-2">
      <div className="form-group col-md-4">
        <label className='sub-headings'>Name</label>
        <input type="text" onChange={e=>{setAppName(e.target.value)}} className="form-control form-input" />
      </div>
      <div className="form-group col-md-4">
        <label className='sub-headings' >Display</label>
        <input type="file" className="form-control form-input" onChange={e => {
						setFile(e.target.files[0])
					}} />
      </div>
    </div>
    <div className="form-group btn-group mt-4">
        <button className="btn form-btn">Cancel</button>
        <button type="submit" className="btn form-btn-create ml-2">Create</button>
    </div>
  </form>
  )
};

