import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'
import urls from '../../utils/endPoints';

export default function LoginForm() {
    const [useremail, setUserEmail] = useState('');
    const [password, setPassword] = useState('');

    let history = useHistory()
 
    let login = async (e) => {
        e.preventDefault();
        console.log(useremail,password)
        if (!useremail || useremail === "") {
            return alert("Please provide email");
        }
        if (!password || password === "") {
            return alert("Please provide password");
        }
        if (password.length < 7) {
            return alert("Password length should be atleast 8 characters");
        }
        let Response = await axios.post(`${urls.base_url}/login`, {
            email: useremail,
            password: password,
        });
        console.log(Response.data)
        if (Response.data.status === true) {
            history.push('/home')
        } else {
            return alert("Invalid credentials");
        }
    };
    return (
        <div className="login-form col-lg-8 col-xs-10 m-auto">
            <div className='row justify-content-center'>
                <span className="welcome-span text-center">Welcome Back!</span>
            </div>
            <div className='row justify-content-center'>
                <span className="text-in-form">To login please enter your email and password</span>
            </div>
            <div className="row justify-content-center">
                <div className="col-lg-8 col-sm-10 ">
                    <div className="fields-container">
                        <div className="auth-form-input-fields">
                            <input type="email"
                                className="form-control"
                                id="email-address"
                                placeholder="Enter your email"
                                onChange={(e) => setUserEmail(e.target.value)}
                            />
                            <input type="password"
                                className="form-control"
                                id="password"
                                placeholder="Enter your password"
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </div>
                        <div className="container-remember">
                            <div id="left">
                                <input type="checkbox" className="btn-check" id="btncheck3" autoComplete="off" />
                                <span className="text-in-form">Remember me</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='row justify-content-center'>
                <div className="auth-submit-btn">
                    <button type="button" className="btn btn-warning btn-md p-1" onClick={login}>Login</button>
                </div>
            </div>
            <div className='row justify-content-center'>
                <span className="text-in-form">Don't have an account ? <Link to="/sign-up">Create account</Link></span>
                
            </div>

        </div>
    )
}
