import React from 'react';
import Positions from './Positions/Positions';
import { RoomDetailsTable } from './RoomDetailsTable';
import './RoomDetails.css'
import axios from 'axios';
import urls from '../../../../../utils/endPoints'


const RoomDetails = (props) => {
  const [battlers,setBattlers] = React.useState([])

  React.useEffect(()=>{
    axios.post(`${urls.base_url}/admin/room-details`,{
      room_id:props.match.params.id
    })
    .then(res=>{
      console.log(res.data.battlers)
      setBattlers(res.data.battlers)
    })
    .catch(err=>{
      console.log(err)
    })
  },[])
  return (
  <div>
    <p className='heading mt-2' >Battle Details</p>
    <Positions battlers={battlers}/>
    <RoomDetailsTable battlers={battlers}/>
  </div>
)};

export default RoomDetails;
