import { useState } from "react";
import { Link } from "react-router-dom";
import newlogo from "../../assets/new-logo.png";
import gameIcon from "../../assets/game-icon.png";
import dashboardIcon from "../../assets/dashboard-icon.png";
import paypalIcon from "../../assets/paypal-icon.png";
import userManagementIcon from "../../assets/user-management-icon.png";
import paymentIcon from "../../assets/payment-icon.png";

import "./Sidebar.css";

const Sidebar = ({ sidebarOpen, closeSidebar }) => {

  let [tabSelected, setTabSelected] = useState('Dashboard')
  let sideBarElements = [
    {
      name: "Dasboard",
      icon: dashboardIcon,
      path: '/home',
      component: {}
    },
    {
      name: "Game Management",
      icon: gameIcon,
      path: '/home/games',
      component: {}
    },
    {
      name: "User Management",
      icon: userManagementIcon,
      path: '/home/user-management',
      component: {}
    },
    {
      name: "Payment Management",
      icon: paymentIcon,
      path: '/home/payment',
      component: {}
    },
    {
      name: "Paypal settings",
      icon: paypalIcon,
      path: '/home/paypal',
      component: {}
    },
  ]

  return (
    <div className={sidebarOpen ? "sidebar_responsive" : ""} id="sidebar">
      <div className="sidebar__title">
        <div className="sidebar__img">
          <img src={newlogo} alt="logo" />
        </div>
        <i
          onClick={() => closeSidebar()}
          className="fa fa-times"
          id="sidebarIcon"
          aria-hidden="false"
        ></i>
      </div>

      <div className="sidebar__menu">
        {
          sideBarElements.map((item, index) => (
            <Link key={index} to={item.path}>
              <div key={index}
                className={item.name === tabSelected ? "sidebar__link active_menu_link row sidebar-container" : "sidebar__link row sidebar-container"}
                onClick={() => {
                  setTabSelected(item.name)
                }}>
                {/* <i className={item.icon}></i>
                <a href="#">{item.name}</a> */}
                <div className="icon-container">
                  <img src={item.icon} className="game-icon" alt='icon'></img> 
                </div>
                <div className="link-container">
                  <span className="pl-4">{item.name}</span>
                </div>
              </div>
            </Link>
          ))
        }
      </div>
    </div>
  );
};

export default Sidebar;
